.social-list {
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: 150px;
    margin: 0;
    padding: 0;
    list-style: none;
}

.social-item {
    margin: 20px 0 0 30px;
    font-size: 24px;
    -webkit-transition: all ease-in-out 0.3s;
    transition: all ease-in-out 0.3s;
}
.social-item a {
    color: #623296;
}

.social-item:first-child {
    margin-left: 0;
}

.social-item.twitter a:hover {
    color: #1da1f2;
    -webkit-transition: all ease-in-out 0.3s;
    transition: all ease-in-out 0.3s;
}

.social-item.facebook a:hover {
    color: #3b5998;
    -webkit-transition: all ease-in-out 0.3s;
    transition: all ease-in-out 0.3s;
}

.social-item.instagram a:hover {
    color: #e4405f;
    -webkit-transition: all ease-in-out 0.3s;
    transition: all ease-in-out 0.3s;
}
header {
  padding: 20px 0px;
  overflow: hidden;
  background-color: #fff;
}

header .container {
  display: flex;
  justify-content: space-between;
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
  overflow: hidden;
  box-sizing: border-box;
}

.header-brand {
    float: left;
}

.header-logo-img {
  margin: 0px 20px 0px 0px;
  height: 40px;
  vertical-align: middle;
}

.header-slogan {
  color: #999;
  display: inline-block;
  font-size: 12px;
  margin: 0px;
  vertical-align: 2px;
}

.header-slogan-slash {
  color: rgba(0,0,0,0.1);
  font-size: 46px;
  font-weight: 300;
  margin: 0px 10px 0px 0px;
  vertical-align: middle;
}
.header-slogan-text {
  vertical-align: middle;
  font-weight: 600;
}



@media (min-width: 1200px) {
  header .container {
      width: 992px;
  }
}
@media (min-width: 992px) and (max-width: 1199px) {
  header .container {
    width: 970px;
  }
}
@media (min-width: 768px) and (max-width: 991px) {
  header .container {
    width: 750px;
  }
}

@media (max-width: 768px) {
  header{
    height: 103px;
    box-sizing: border-box;
  }
  .header-slogan {
    display: none;
  }
  .header-brand {
    float: none;
    float: initial;
  }
}

.main-title-wrapper {
    display: flex;
    width: 100%;
    max-width: 1733px;
    height: 200px;
    margin: 0 auto;
    background-image: url('https://openqube.io/img/slider_bg.jpg'); 
    background-position: 0;
}

.main-title {
    display: inline-block;
    align-self: center;
    padding: 0 20px;
    margin: 11px auto;
    color: #f7f7f7;
    font-family: 'Open Sans', sans-serif;
    font-size: 28px;
    font-weight: 300;
    box-sizing: border-box;
    text-transform: uppercase;
}

.main-title span {
    margin: 0;
    line-height: 40px;
}

.main-title .first-line {
    font-size: 22px;
}

@media (min-width: 340px) and (max-width: 768px) {
    .main-title {
        margin-top: 11px;
    }
}

@media (max-width: 768px) {
    .main-title-wrapper {
        height: 160px;
    }
    .main-title {
        padding: 0 20px;
        font-size: 22px;
    }
}

.container {
    display: inline-flex;
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
    box-sizing: border-box;
}


@media (min-width: 1200px) {
  .container {
      width: 992px;
  }
}
@media (min-width: 992px) and (max-width: 1199px) {
  .container {
    width: 970px;
  }
}
@media (min-width: 768px) and (max-width: 991px) {
