.category-title-wrapper {
  padding-bottom: 10px;
}

.title {
  text-align: left;
}

.title a {
  text-decoration: none;
  color: #623296;
}

.title.category {
  padding: 30px 0 20px;
  margin: 0 auto;
  text-align: center;
  font-size: 26px;
  font-weight: 300;
  background-color: #f7f7f7;
}

.title.subcategory {
