}

.color-bar {
  height: 3px;
  margin: 0 auto;
  line-height: 0;
}
.color-bar span {
  display: inline-block;
  width: calc(100% / 3);
  height: 3px;
}
.color-bar-section-1 {
  background-color: #623296;
}
.color-bar-section-2 {
  background-color: #9c4dcc;
