}
.color-bar-section-3 {
  background-color: #c158dc;
  background-color: #F4A04B;
}

@media (max-width: 460px) {
  .title.category {
    font-size: 22px;
  }

  .title.subcategory {
    margin: 20px 0 10px;
    font-size: 18px;
    text-align: center;
  }

  .title.section {
    margin: 20px 0 10px;
    border: none;
    font-size: 14px;
    text-align: center;
  }
}
.tabs-item {
    display: inline-block;
    padding: 10px 20px;
    border: 1px solid #e9e9e9;
    border-bottom-color: transparent;
    margin: 0 2px -1px 0;
    font-size: 14px;
    cursor: pointer;
    color: #363636;
    box-sizing: border-box;
}

.tabs-item.active {
    border-top: 2px solid #ED5C88;
    border-bottom-color: #FFF; 
    color: #623296;
}

@media (max-width: 460px) {
    .tabs-item{
        display: block;
        width: 100%;
        text-align: center;
        font-size: 12px;
    }
  }

.tabs-list {
    list-style: none;
    text-align: left;
    padding: 0;
    border-bottom: 1px solid #e9e9e9;
    box-sizing: border-box;
}

.tab-content {
    text-align: left;
}

.tab-content-caption {
    font-size: 0.8em;
    font-style: italic;
    text-align: center;
}

.tab-content-description {
    margin-top: 10px;
    font-size: 0.8em;
