/*TOOLTIP*/
.tooltip {
    position: relative;
    margin: 0;
    padding: 15px;
    list-style: none;
    border: 1px solid #e9e9e9;
    background-color: rgba(255, 255, 255, 0.9);
}

.tooltip-item {
    margin: 5px 0 0 0;
    z-index: 10;
}

.tooltip-item .name, 
.tooltip-item .value {
    display: block;
    margin: 0 auto;
    text-align: center;
}

.tooltip-item .name {
    font-size: 12px;
    z-index: 100;
    font-weight: 700;
}

.tooltip-item .value {
    font-size: 20px;
}

.no-data {
    font-style: italic;
    font-size: 0.75em;
}

.invalid {
    font-style: italic;
    font-size: 0.75em;
    color: #ccc;
}

.meme {
    width: 150px;
    margin-top: 15px;
}
.pie-chart {
    margin-left: 25%;
}

.pie-chart svg {
    overflow: visible;
    font-size: 0.85rem;
}
.line-chart {
    margin: 0 auto;
}
.area-chart {
    margin: 0 auto;
}
.map-argentina-container {
    position: relative;
    width: 100%;
    margin: 0 auto;
}

.map-argentina-container path:hover {
    stroke: #ffb57c;
    stroke-width: 2px;
}

.map-argentina-container .continent svg {
    width: 75%;
}

.map-argentina-container .caba {
    position: absolute;
    right: 0;
    top: 20%;
}

.map-argentina-container .caba svg {
    width: 75%;
}

.map-argentina-container .malvinas {
    position: absolute;
    left: 120px;
    top: 410px;
}

.description {
    position: absolute;
    bottom: 180px;
    right: 0;
    width: 110px;
    padding: 10px;
    text-align: center;
    border: 1px solid #e9e9e9;
    font-size: 12px;
}

.description p {
    margin: 0;
}
.province-name {
    color: #623296;
}
.province-value {
    font-size: 22px;
    color: #623296;
    font-weight: 600;
}

@media(max-width: 350px) {
    .map-argentina-container {
        width: 320px;
    }
    .map-argentina-container .caba {
        right: 30px;
    }
    .description {
        right: 30px;
        padding: 3px;
        font-size: 10px;
    }
    .province-value {
        font-size: 14px;
    }
}

@media(max-width: 460px) {
    .map-argentina-container {
        width: 320px;
    }
}

@media(min-width: 461px) {
    .map-argentina-container {
        width: 400px;
    }
}
.sticky-wrapper {
    width: calc((100% / 12 * 3) - 10px);
    min-height: 100vh;
}
.side-nav-wrapper {
    width: 100%;
    top: 0;
    margin-right: 10px; 
    border: 1px solid #e9e9e9;
    border-radius: 3px;
    box-sizing: border-box;
    max-height: 90vh;
    overflow: scroll;
    box-sizing: border-box;
}

.side-nav {
    width: 100%;
}

.side-nav-wrapper ul {
    padding: 10px 10px 20px 20px;
    list-style: none;
    box-sizing: border-box;
    counter-reset: item;
}

.side-nav-wrapper li {
    text-align: left;
}

.side-nav-wrapper li:before {
    content: counters(item, ".") ".";
    counter-increment: item;
    font-size: 0.75em;
    margin: 0 5px;
    opacity: 0.6;
    color: #623296;
}

.side-nav-wrapper li.is-current:before {
    opacity: 1;
}

.side-nav-wrapper li a {
    color: #623296;
    text-decoration: none;
}

li.is-current > a {
   color: #311b49;
   font-weight: 700;
}

.item-title {
    font-size: 12px;
}

@media (max-width: 768px) {
    .sticky-wrapper {
        display: none;
    }
}
